import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';
import BulmaTest from '@/components/BulmaTest';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/bulma',
      name: 'BulmaTest',
      component: BulmaTest,
    },
  ],
  mode: 'history',
});



